@tailwind base;
@tailwind components;
@tailwind utilities;

/* Global CSS */
@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');

body {
  margin: 0;
  font-family: "Poppins", "bold";
  -webkit-font-smoothing: "Poppins", "bold";
  -moz-osx-font-smoothing: "Poppins", "bold";
  font-style: normal;
  background: linear-gradient(-90deg, #d9e3f1, #edd3ee, #EAEAF6, #c8c8d8);
  background-size: 100vw 100vh;
  animation: gradient 15s ease infinite;
  height: 100vh;
}

@keyframes gradient {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

.App circle {
  width: 20vmin;
  height: 20vmin;
  z-index: -1;
  border-radius: 20vmin;
  backface-visibility: hidden;
  position: absolute;
  animation-name: move;
  animation-duration: 6s;
  animation-timing-function: linear;
  animation-iteration-count: infinite;
}

.App circle:nth-child(1) {
  color: #b7a7d2;
  top: 50%;
  left: 70%;
  animation-duration: 10.6s;
  animation-delay: -8.7s;
  transform-origin: 5vw -4vh;
  box-shadow: 40vmin 0 15vmin currentColor;
}

.App circle:nth-child(2) {
  color: #edd3ee;
  top: 40%;
  left: 30%;
  animation-duration: 13.1s;
  animation-delay: -9.3s;
  transform-origin: 12vw -7vh;
  box-shadow: -40vmin 0 12vmin currentColor;
}

.App circle:nth-child(3) {
  color: #b7a7d2;
  top: 50%;
  left: 70%;
  animation-duration: 10.6s;
  animation-delay: -8.7s;
  transform-origin: 5vw -4vh;
  box-shadow: 40vmin 0 15vmin currentColor;
}

.App circle:nth-child(4) {
  color: #edd3ee;
  top: 40%;
  left: 30%;
  animation-duration: 13.1s;
  animation-delay: -9.3s;
  transform-origin: 12vw -7vh;
  box-shadow: -40vmin 0 12vmin currentColor;
}

.App circle:nth-child(5) {
  color: #b7a7d2;
  top: 50%;
  left: 70%;
  animation-duration: 10.6s;
  animation-delay: -8.7s;
  transform-origin: 5vw -4vh;
  box-shadow: 40vmin 0 15vmin currentColor;
}

.App circle:nth-child(6) {
  color: #edd3ee;
  top: 40%;
  left: 30%;
  animation-duration: 13.1s;
  animation-delay: -9.3s;
  transform-origin: 12vw -7vh;
  box-shadow: -40vmin 0 12vmin currentColor;
}

.App circle:nth-child(7) {
  color: #b7a7d2;
  bottom: 50%;
  right: 70%;
  animation-duration: 10.6s;
  animation-delay: -8.7s;
  transform-origin: 5vw -4vh;
  box-shadow: 40vmin 0 15vmin currentColor;
}

.App circle:nth-child(8) {
  color: #edd3ee;
  bottom: 40%;
  right: 30%;
  animation-duration: 13.1s;
  animation-delay: -9.3s;
  transform-origin: 12vw -7vh;
  box-shadow: -40vmin 0 12vmin currentColor;
}

@keyframes move {
  100% {
    transform: translate3d(0, 0, 1px) rotate(360deg);
  }
}


h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
}

:root {
  --dark-purple: #3B3355;
  --light-purple: #5D5D81;
  --white: #FEFCFD;
  --ice-blue: #BFCDE0;
  --black: #000505;
  --bg: #EAEAF6;
}

.btn {
  font-family: "Poppins", "bold";
  display: inline-block;
  padding: 8px 18px;
  margin: 5px;
  font-size: 32px;
  font-style: normal;
  font-weight: 700;
  line-height: 24 px;
  cursor: pointer;
  text-align: center;
  text-decoration: none;
  border-radius: 50px;
  --webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  -ms-border-radius: 5px;
  -o-border-radius: 5px;
  transition: background-color 0.5s;
  --webkit-transition: background-color 0.5s;
  -moz-transition: background-color 0.5s;
  -ms-transition: background-color 0.5s;
  -o-transition: background-color 0.5s;
}

.btn-outline-primary {
  color: var(--dark-purple);
  background-color: var(--ice-blue);
  border: 3px solid var(--dark-purple);
}

.btn-outline-primary:hover {
  color: var(--ice-blue);
  background-color: var(--dark-purple);
}

.btn-primary {
  color: var(--ice-blue);
  background-color: var(--dark-purple);
  border: 0px solid var(--dark-purple);
}

.btn-primary:hover {
  color: var(--dark-purple);
  background-color: var(--ice-blue);
}

.btn-contacts {
  color: var(--ice-blue);
  background-color: var(--dark-purple);
  border: 0px solid var(--dark-purple);
  display: inline-block;
  padding: 8px 18px;
  cursor: pointer;
  border-radius: 25px;
  --webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  -ms-border-radius: 5px;
  -o-border-radius: 5px;
  transition: background-color 0.5s;
  --webkit-transition: background-color 0.5s;
  -moz-transition: background-color 0.5s;
  -ms-transition: background-color 0.5s;
  -o-transition: background-color 0.5s;
}

.btn-contacts:hover {
  color: var(--dark-purple);
  background-color: var(--ice-blue);
}

