/* Section Title*/



/* Navbar Style Start */
.navbar {
  display: flex;
  justify-content: space-between;
  align-items: right;
  padding: 15px 85.333px;
  background: var(--white);
  box-shadow: 0px 5.333px 80px 0 rgba(0,0,0,0.1);
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 9999;
}

.navbar--items > ul {
  list-style: none;
  display: flex;
  align-items: flex-start;
  gap: 45px;
  text-decoration: none;
}

.navbar--items ul > li > a {
  text-decoration: none;
}

/* Navbar Content */
.navbar--content {
  color: var(--light-purple);
  font-size: 20px;
  font-style: normal;
  font-weight: 700;
  line-height: 30px;
  cursor: pointer;
}

.navbar--content:hover {
  color: var(--ice-blue);
  text-decoration: underline;
}

.navbar--active--content {
  color: var(--ice-blue);
}

/* Navbar Style Ends */


/* Hero Section Style */

.hero--section--content--box {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 20px;
  padding-top: 50px;
  color: var(--light-purple);
}

.hero--name {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  color: var(--dark-purple);
  align-self: stretch;
}

.hero--section--content {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 5px;
}

.hero--section--content--box > button {
  margin-top: 21.333px;
}

.hero--img {
  display: flex;
  padding-top: 50px;
  flex-direction: column;
}

.hero--img > img {
  width: 75%;
  height: 75%;
  flex-direction: column;
}

.social-media-icons a {
  color: var(--ice-blue);
  padding: 15px;
  margin: 5px;
  background-color: var(--dark-purple);
  cursor: pointer;
  text-align: center;
  text-decoration: none;
  border-radius: 50px;
  display: inline-flex;
  --webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  -ms-border-radius: 5px;
  -o-border-radius: 5px;
  transition: background-color 0.5s;
  --webkit-transition: background-color 0.5s;
  -moz-transition: background-color 0.5s;
  -ms-transition: background-color 0.5s;
  -o-transition: background-color 0.5s;
  display: inline-block;
  padding: 8px 18px;
  margin: 5px;
  font-size: 32px;
  font-style: normal;
  font-weight: 700;
  line-height: 24 px;
  cursor: pointer;
  text-align: center;
}

.social-media-icons a:hover {
  color: var(--dark-purple);
  font-size: 2rem;
  background-color: var(--ice-blue);
}

/* Hero Section Style Ends */

/* About Section Style Begins */
.about-text {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  color: var(--light-purple);
  align-self: stretch;
  padding-left: 100px;
  padding-bottom: 25px;
}

.about--section--box {
  display: grid;
  gap: 100px;
  grid-template-columns: repeat(2, 1fr)
}

.timeline-header {
  display: inline-flex;
  gap: 50px;
  align-items: flex-start;
  padding-bottom: 25px;
  z-index: 0;
}

.vertical-timeline-education {
  align-items: flex-start;
  z-index: 0;
  margin-right: 100px;
}

.vertical-timeline-element-title {
  color: var(--dark-purple);
  z-index: 0;
}
/* About Section Style Ends */


/* Skills Section Style Starts */
.skills--section {
  z-index: 0;
}

.skills--lang--title {
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: justify;
}

.lang-lib-skills {
  color: var(--ice-blue);
  font-size: 3rem;
  margin: auto;
  cursor: pointer;
  text-align: center;
  display: flex;
}


.lang-lib-skills:hover{
  opacity: 5;
  box-shadow: var(--dark-purple);
}

.tech-icons {
  padding: 15px;
  border-color: var(--light-purple);
  border-radius: 50px;
  transform: scale(1) !important;
  opacity: 75%;
  display: inline-flex;
  background-color: var(--dark-purple);
}

.tech-icons h3 {
  padding-left: 10px;
}

.tech-icons:hover {
  opacity: 1;
  transform: scale(1) !important;
  overflow: hidden !important;
  border: 0px solid var(--light-purple) !important;
  box-shadow: 0px 0px 10px 5px var(--ice-blue);
}

.tech-text {
  color: var(--dark-purple);
  transition: opacity 0.3s ease-in-out;
  margin-left: 20px;
}

.svg-icon {
  width: 30px;
  height: 30px;
  fill: var(--light-purple);
}
/* Skills Section Style Ends */


/* Projects Style Starts */
.projects--title {
  color: var(--dark-purple);
  font-size: 64px;
  text-align: left;
  font-style: normal;
  font-weight: 700;
  line-height: 60px;
  display: flex;
  align-items: flex-start;
}

.portfolio--section {
  display: flex;
  padding: 100px;
  flex-direction: column;
  align-items: flex-start;
  gap: 30px;
}
.portfolio--container-box {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}
.portfolio--container {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}
.portfolio--section--container {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 42.6px;
  width: 100%;
}
.portfolio--section--img {
  border-radius: 8px;
  /* width: 100%; */
}

.portfolio--section--img > img {
  /* width: 100%; */
  /* height: 200px; */
  border-top-left-radius: 12px;
  border-top-right-radius: 12px;
}

.portfolio--section--card {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 32px;
  height: auto;
  background: var(--white);
  box-shadow: 0 0 32px 0 rgba(0, 0, 0, 0.15);
  cursor: pointer;
  border-top-left-radius: 12px;
  border-top-right-radius: 12px;
  border-bottom-left-radius: 12px;
  border-bottom-right-radius: 12px;
}

.portfolio--section--card--content {
  display: flex;
  padding: 32px;
  flex-direction: column;
  align-items: flex-start;
  gap: 32px;
  align-self: stretch;
  color: var(--light-purple);
}

.portfolio--section--title {
  color: var(--dark-purple);
}

.portfolio--link {
  text-decoration: none;
  color: var(--ice-blue);
  display: flex;
  align-items: center;
  justify-content: center;
  margin: auto;
  gap: 10px;
  background-color: var(--light-purple);
  padding: 10px;
  border-radius: 50px;
}

.modal-btn {
  border: 0px;
  background-color: var(--white);
  z-index: 990;
} 

.portfolio--link:hover {
  color: var(--light-purple);
  background-color: var(--ice-blue);
  transition: background-color 0.5s;
}

.fullscreen-overlay {
  position: fixed;      /* Position the element relative to the viewport */
  top: 0;               /* Align to the top of the screen */
  left: 0;              /* Align to the left of the screen */
  right: 0;             /* Align to the right of the screen */
  bottom: 0;            /* Align to the bottom of the screen */
  width: 100%;          /* Ensure it covers the full width */
  height: 100%;         /* Ensure it covers the full height */
  background-color: var(--bg);
  z-index: 9999;
  display: flex;
  justify-content: center;
  align-items: center;
}

.modalContainer {
  background-color: white;
  box-shadow: var(--light-purple) 0px 5px 15px;
  padding: 25px;
}


/* Projects Style Ends */

/* About, Projects */
.section--title {
  color: var(--dark-purple);
  font-size: 64px;
  padding-left: 100px;
  text-align: left;
  font-style: normal;
  font-weight: 700;
  line-height: 60px;
  display: flex;
  align-items: flex-start;
}


/* Hamburger menu  */

.nav__hamburger {
  display: none;
  width: 1.875rem;
  height: 1.313rem;
  flex-direction: column;
  justify-content: space-around;
  position: absolute;
  top: 25px;
  right: 25px;
}

.nav__hamburger__line {
  display: block;
  height: 0.188rem;
  width: 100%;
  background-color: var(--light-purple);
  border-radius: 0.625rem;
  transition: all ease-in-out 0.2s;
}

.nav__hamburger:hover {
  cursor: pointer;
}

@media screen and (max-width: 1200px) {
  .nav__hamburger {
    display: flex;
    z-index: 200;
  }
  .navbar--items {
    display: flex;
  }
  .navbar--items {
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: rgba(255, 255, 255);
    top: -20rem;
    left: 0;
    width: 100%;
    transition: all ease-in-out 0.4s;
  }
  .navbar--items ul {
    flex-direction: column;
    align-items: center;
    width: 100%;
    padding: 3rem 0 0.6rem;
    z-index: -100;
  }
  .navbar--items ul li {
    text-align: center;
  }
  .navbar--items ul li a {
    padding: 0.5rem;
  }
  .navbar--items {
    z-index: -1000;
  }
  .navbar--items.active {
    top: 30px;
  }
  .nav__hamburger.active :nth-child(1) {
    transform: rotate(45deg) translate(0.45rem, 0.1875rem);
  }

  .nav__hamburger.active :nth-child(2) {
    opacity: 0;
  }

  .nav__hamburger.active :nth-child(3) {
    transform: rotate(-45deg) translate(0.45rem, -0.1875rem);
  }
}
